var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("蝙蝠女")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        “蝙蝠女”是由DC Comics出版的美国漫画书中出现的几个虚构的超级英雄的名字，描绘为超级英雄蝙蝠侠的女性同行。虽然角色贝蒂凯恩于1961年由Bill Finger和Sheldon Moldoff作为蝙蝠女孩出版，但她于1967年被Barbara Gordon取代，后来被认定为标志性的Batgirl。这个角色在侦探漫画＃359中首次亮相，名为“蝙蝠女的百万美元首演！” （作家Gardner Fox和艺术家Carmine Infantino于1967年1月创作），作为警察局长詹姆斯戈登的女儿介绍。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Batgirl在高谭市经营，与蝙蝠侠和原来的罗宾，迪克格雷森，以及其他蒙面的治安维持者结盟。这个角色经常出现在“ 侦探漫画”，“ 蝙蝠侠家庭 ”以及哥伦比亚特区出版的其他几本书中，直到1988年。那一年，芭芭拉·戈登出现在Barbara Kesel的Batgirl Special＃1中，在那里她退出了打击犯罪的行列。随后，她出现在艾伦摩尔的图画小说“ 蝙蝠侠：杀戮笑话”中，在她的平民身份中，她被小丑射杀并留下了截瘫。虽然她在第二年被编辑金耶尔和作家约翰奥斯特兰德重新设想为计算机专家和信息经纪人甲骨文，但她的瘫痪引发了关于漫画中女性形象的争论，特别是针对女性角色的暴力行为。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        在1999年的故事情节“ 无人之地 ”中，被称为Huntress的海伦娜·贝尔蒂内（Helena Bertinelli）角色简短地扮演了蝙蝠女（Batgirl）的角色，直到蝙蝠侠因违反严格的规范而被剥夺了身份。在同一故事情节中，人物Cassandra Cain被介绍。该隐是作为刺客David Cain和Shiva夫人的女儿而写的并在蝙蝠侠和甲骨文的指导下掌握Batgirl的衣钵。2000年，她成为第一位出演同名月刊漫画系列的蝙蝠女，此外她还成为美国漫画中出现的亚洲血统最突出的角色之一。该系列节目在2006年被取消，此时在全公司的故事情节“ 一年后 ”中，她成为了一个反派和刺客联盟的负责人。在收到读者的苛刻反馈后，她后来恢复了原来的观念。然而，在Cassandra Cain放弃角色之后，角色Stephanie Brown，最初被称为Spoiler和后来的Robin，接替她成为Batgirl。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Stephanie Brown 于2009年至2011年成为Batgirl系列的特色。随后，DC在重新推出的新52期间重新推出了所有月刊。在修订后的连续性中，Barbara Gordon从外科手术后的瘫痪状态中恢复过来，并重新启动了Batgirl系列中的明星作为名义角色。这些变化作为2016 DC重生活动的一部分保留。作为Batgirl，芭芭拉戈登已被改编成与蝙蝠侠特许经营有关的各种媒体，包括电视，电影，动画，电子游戏和其他商品。角色的适应性受到影响，决定让她重返漫画，Dan DiDio，DC Comics的联合出版商，表示她是这个角色最着名的版本。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"})])}
